/* Container for the slideshow */
.your-slideshow-container {
    text-align: center;
    max-width: 500px; /* Adjust the maximum width as needed */
    margin: 0 auto; /* Center the container */
}

/* Post title styling */
.your-slideshow-container h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

/* Featured image styling */
.your-slideshow-container img {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
}

/* Date styling */
.your-slideshow-container p {
    font-size: 0.9rem;
    color: #999;
    margin-bottom: 20px;
}

/* Navigation button styling */
.your-slideshow-container button {
    background-color: #0073e6;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    margin: 0 10px;
    border-radius: 5px;
    outline: none;
}

/* Loading message styling */
.your-slideshow-container p.loading {
    font-size: 1rem;
    color: #0073e6;
    font-weight: bold;
}
